  //canvas.toDataURLËøîÂõûÁöÑÊòØ‰∏Ä‰∏≤Base64ÁºñÁ†ÅÁöÑURL,ÂΩìÁÑ∂,ÊµèËßàÂô®Ëá™Â∑±ËÇØÂÆöÊîØÊåÅ
