/**
 * @description ÂàùÂßãÂåñÁΩëÊòìÈ™åËØÅÁ†Å
 * @param {object} config È™åËØÅÁ†ÅÈÖçÁΩÆÂØπË±°
 */
export const initNECaptcha = (config = {}) => {
  if (!window.initNECaptcha) return;
  window.initNECaptcha(
    {
      captchaId: config.captchaId || CAPTCHA_ID,
      element: config.element || "",
      mode: config.mode || "popup",
      width: config.width || 280,
      // lang: config.lang === 'zh-CN' ? 'zh-CN' : 'en',
      onReady: (instance) => {
        // È™åËØÅÁ†Å‰∏ÄÂàáÂáÜÂ§áÂ∞±Áª™ÔºåÊ≠§Êó∂ÂèØÊ≠£Â∏∏‰ΩøÁî®È™åËØÅÁ†ÅÁöÑÁõ∏ÂÖ≥ÂäüËÉΩ
        if (config.debug) {
          console.log("[NECaptcha] onReady");
        }
        config.onReady &&
          typeof config.onReady === "function" &&
          config.onReady(instance);
      },
      onVerify: (err, data) => {
        // È™åËØÅÁªìÊûú
        if (config.debug) {
          console.log("[NECaptcha] onVerify", data.validate);
        }
        if (err && config.debug) {
          console.error(err);
          return;
        }

        config.onVerify &&
          typeof config.onVerify === "function" &&
          config.onVerify(data);
      },
    },
    (instance) => {
      // ÂàùÂßãÂåñÊàêÂäü
      if (config.debug) {
        console.log("[NECaptcha] Initialized");
        console.log("[NECaptcha] instance", instance);
      }
      instance.popUp();

      config.onLoad &&
        typeof config.onLoad === "function" &&
        config.onLoad(instance);
    },
    (err) => {
      // ÂàùÂßãÂåñÂ§±Ë¥•
      if (config.debug) {
        console.error("[NECaptcha] Fail to initialize", err);
      }
      config.onError &&
        typeof config.onError === "function" &&
        config.onError();
    }
  );
};

// ‰∏ãÂàíÁ∫øËΩ¨È©ºÂ≥∞
export function optionsToHump(options) {
  const result = {};
  Object.keys(options).forEach((key) => {
    const newKey = key.replace(/_(\w)/g, (all, letter) => letter.toUpperCase());
    result[newKey] = options[key];
  });
  return result;
}

// È©ºÂ≥∞ËΩ¨Êç¢‰∏ãÂàíÁ∫ø
export function optionsToLine(options) {
  const result = {};
  Object.keys(options).forEach((key) => {
    const newKey = key.replace(/([A-Z])/g, "_$1").toLowerCase();
    result[newKey] = options[key];
  });
  return result;
}

/**
 * @description ÊääÊó∂Èó¥Êà≥ËΩ¨‰∏∫ JS Êó∂Èó¥Êà≥ÔºàÊØ´ÁßíÔºå13‰ΩçÔºâ
 * @param {number} [num=0] ÂæÖËΩ¨Êç¢ÁöÑÊó∂Èó¥Êà≥
 * @returns {number} 13 ‰ΩçÊó∂Èó¥Êà≥
 */
export const ensureMilliseconds = (num = 0) => {
  return Number(String(num).padEnd(13, 0).substr(0, 13));
};

/**
 * @description Ëé∑ÂèñqueryÂèÇÊï∞
 * @param {String} key
 * @returns {String} param
 */
export function getQueryParam(key) {
  const reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)", "i");
  const r = window.location.search.substr(1).match(reg);
  if (r != null) {
    return decodeURI(r[2]);
  }
  return null;
}

/**
 * @description ÂºπÂá∫ËΩØÈîÆÁõòÔºåÈöêËóèÂ∫ïÈÉ® fixed button
 * @param {function} callback
 * @returns {void}
 */
export function compatibleFixedButton(callback) {
  const windowInnerHeight = window.innerHeight;
  window.onresize = () => {
    if (window.innerHeight < windowInnerHeight) {
      callback && callback(false); // ÈöêËóè
    } else {
      callback && callback(true); // ÊòæÁ§∫
    }
  };
}

/**
 * @description Âà§Êñ≠ÊòØÂê¶ÊòØÂæÆ‰ø°ÊµèËßàÂô®ÁéØÂ¢É
 * @returns {boolean}
 */
export function isWxAgent() {
  const ua = navigator.userAgent.toLowerCase(); //Ëé∑ÂèñÂà§Êñ≠Áî®ÁöÑÂØπË±°
  const match = ua.match(/MicroMessenger/i);
  if (!match) return false;
  return match.toString() === "micromessenger";
}

/**
 * @description ÈöêËóèËÅîÁ≥ªÂÆ¢ÊúçÊåâÈíÆ
 * @returns {void}
 */

export function hideChatButton() {
  let iframes = document.querySelectorAll("iframe");
  const len = iframes.length || 0;
  if (len <= 0) return;
  for (let i = 0; i < len; i++) {
    let win = iframes[i].contentWindow;
    let button = win.document.querySelector("#Embed");
    if (button) {
      button.style.display = "none";
    }
  }
}
/**
 * @description Âà§Êñ≠ÁªàÁ´Ø
 * @returns {void}
 */
export function isIosOrAndroid() {
  var u = navigator.userAgent,
    app = navigator.appVersion;
  var isAndroid = u.indexOf("Android") > -1 || u.indexOf("Adr") > -1; //androidÁªàÁ´Ø
  if (isAndroid) {
    return 1;
  }
  var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //iosÁªàÁ´Ø
  if (isiOS) {
    return 2;
  }
  return 0;
}
export function isWeixn() {
  let ua = navigator.userAgent.toLowerCase();
  let isWeixin = ua.indexOf("micromessenger") != -1;
  if (isWeixin) {
    return true;
  } else {
    return false;
  }
}
export function base64ToBlob({
  b64data = "",
  contentType = "",
  sliceSize = 512,
} = {}) {
  return new Promise((resolve, reject) => {
    // ‰ΩøÁî® atob() ÊñπÊ≥ïÂ∞ÜÊï∞ÊçÆËß£Á†Å
    let byteCharacters = atob(b64data);
    let byteArrays = [];
    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      let slice = byteCharacters.slice(offset, offset + sliceSize);
      let byteNumbers = [];
      for (let i = 0; i < slice.length; i++) {
        byteNumbers.push(slice.charCodeAt(i));
      }
      // 8 ‰ΩçÊó†Á¨¶Âè∑Êï¥Êï∞ÂÄºÁöÑÁ±ªÂûãÂåñÊï∞ÁªÑ„ÄÇÂÜÖÂÆπÂ∞ÜÂàùÂßãÂåñ‰∏∫ 0„ÄÇ
      // Â¶ÇÊûúÊó†Ê≥ïÂàÜÈÖçËØ∑Ê±ÇÊï∞ÁõÆÁöÑÂ≠óËäÇÔºåÂàôÂ∞ÜÂºïÂèëÂºÇÂ∏∏„ÄÇ
      byteArrays.push(new Uint8Array(byteNumbers));
    }
    let result = new Blob(byteArrays, {
      type: contentType,
    });
    result = Object.assign(result, {
      // ËøôÈáå‰∏ÄÂÆöË¶ÅÂ§ÑÁêÜ‰∏Ä‰∏ã URL.createObjectURL
      preview: URL.createObjectURL(result),
      name: `XXX.png`,
    });
    resolve(result);
  });
}

export function imageBase64ToFile(imageBase64) {
  const arr = imageBase64.split(',');
  const binary = atob(arr[1]); // base64Ëß£Á†Å
  const mine = arr[0].match(/:(.*?);/)[1]; // Êñá‰ª∂Á±ªÂûã
  const array = [];
  for (let i = 0; i < binary.length; i++) {
    array.push(binary.charCodeAt(i)); //  Unicode ÁºñÁ†Å
  }
  const blob = new Blob([new Uint8Array(array)], {type: mine}); // BlobÂØπË±°ÂèØ‰ª•ÁúãÂÅöÊòØÂ≠òÊîæ‰∫åËøõÂà∂Êï∞ÊçÆÁöÑÂÆπÂô®ÔºõUint8ArrayÁ±ªÂûãÊï∞ÁªÑÔºö8‰ΩçÊó†Á¨¶Âè∑Êï¥Êï∞Êï∞ÁªÑ
  /**
   * blobPartsÔºö Êï∞ÁªÑÁ±ªÂûãÔºå Êï∞ÁªÑ‰∏≠ÁöÑÊØè‰∏ÄÈ°πËøûÊé•Ëµ∑Êù•ÊûÑÊàêBlobÂØπË±°ÁöÑÊï∞ÊçÆÔºåÊï∞ÁªÑ‰∏≠ÁöÑÊØèÈ°πÂÖÉÁ¥†ÂèØ‰ª•ÊòØArrayBuffer(‰∫åËøõÂà∂Êï∞ÊçÆÁºìÂÜ≤Âå∫), ArrayBufferView,Blob,DOMString„ÄÇÊàñÂÖ∂‰ªñÁ±ª‰ººÂØπË±°ÁöÑÊ∑∑Âêà‰Ωì„ÄÇ
   * optionsÔºö ÂèØÈÄâÈ°πÔºåÂ≠óÂÖ∏Ê†ºÂºèÁ±ªÂûãÔºåÂèØ‰ª•ÊåáÂÆöÂ¶Ç‰∏ã‰∏§‰∏™Â±ûÊÄßÔºö
       typeÔºåÈªòËÆ§ÂÄº‰∏∫""ÔºåÂÆÉ‰ª£Ë°®‰∫ÜÂ∞Ü‰ºöË¢´ÊîæÂÖ•Âà∞blob‰∏≠ÁöÑÊï∞ÁªÑÂÜÖÂÆπÁöÑMIMEÁ±ªÂûã„ÄÇ
       endingsÔºå ÈªòËÆ§ÂÄº‰∏∫"transparent"ÔºåÁî®‰∫éÊåáÂÆöÂåÖÂê´Ë°åÁªìÊùüÁ¨¶\nÁöÑÂ≠óÁ¨¶‰∏≤Â¶Ç‰ΩïË¢´ÂÜôÂÖ•„ÄÇ ÂÆÉÊòØ‰ª•‰∏ã‰∏§‰∏™ÂÄº‰∏≠ÁöÑ‰∏Ä‰∏™Ôºö "native"ÔºåË°®Á§∫Ë°åÁªìÊùüÁ¨¶‰ºöË¢´Êõ¥Êîπ‰∏∫ÈÄÇÂêàÂÆø‰∏ªÊìç‰ΩúÁ≥ªÁªüÊñá‰ª∂Á≥ªÁªüÁöÑÊç¢Ë°åÁ¨¶Ôºõ "transparent"ÔºåË°®Á§∫‰ºö‰øùÊåÅblob‰∏≠‰øùÂ≠òÁöÑÁªìÊùüÁ¨¶‰∏çÂèò„ÄÇ
   */
  // ËΩ¨Êç¢ÊàêfileÂØπË±°
  const file = new File([blob],Date.now() + '.png', {type:mine});
  /**
  * filebitsÔºöArrayBufferÔºåArrayBufferViewÔºåBlobÔºåÊàñËÄÖ DOMString ÂØπË±°ÁöÑ Array ‚Äî ÊàñËÄÖ‰ªª‰ΩïËøô‰∫õÂØπË±°ÁöÑÁªÑÂêà„ÄÇËøôÊòØ UTF-8 ÁºñÁ†ÅÁöÑÊñá‰ª∂ÂÜÖÂÆπ„ÄÇ
  * nameÔºöÊñá‰ª∂ÂêçÁß∞ÔºåÊàñËÄÖÊñá‰ª∂Ë∑ØÂæÑ
  * options ÂèØÈÄâÔºöÈÄâÈ°πÂØπË±°ÔºåÂåÖÂê´Êñá‰ª∂ÁöÑÂèØÈÄâÂ±ûÊÄß„ÄÇÂèØÁî®ÁöÑÈÄâÈ°πÂ¶Ç‰∏ãÔºö
      type: DOMStringÔºåË°®Á§∫Â∞ÜË¶ÅÊîæÂà∞Êñá‰ª∂‰∏≠ÁöÑÂÜÖÂÆπÁöÑ MIME Á±ªÂûã„ÄÇÈªòËÆ§ÂÄº‰∏∫ ‚Äú‚Äù „ÄÇ
      lastModified: Êï∞ÂÄºÔºåË°®Á§∫Êñá‰ª∂ÊúÄÂêé‰øÆÊîπÊó∂Èó¥ÁöÑ Unix Êó∂Èó¥Êà≥ÔºàÊØ´ÁßíÔºâ„ÄÇÈªòËÆ§ÂÄº‰∏∫ Date.now()„ÄÇ
   */
  return file
}